"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";

import { EmptyState } from "@/components/EmptyState";
import { Header } from "@/components/ui/Header";

export default function MaterialPage() {
  const router = useRouter();

  useEffect(() => {
    const checkAuth = async () => {
      const {
        data: { user },
      } = await supabase.auth.getUser();
      if (!user) {
        router.push("/login");
      }
    };

    checkAuth();
  }, [router]);

  return (
    <div className="min-h-screen bg-white">
      <Header />
      <div className="mx-auto flex w-full max-w-[1350px] justify-end px-6 pt-8">
        <button
          type="button"
          onClick={() => router.push("/material/editor")}
          className="min-w-[170px] rounded-lg bg-green-600 px-4 py-3 text-sm font-semibold text-white transition hover:bg-green-500"
        >
          Создать материал
        </button>
      </div>
      <main className="mx-auto flex w-full max-w-[1350px] justify-center px-6 py-12">
        <EmptyState />
      </main>
    </div>
  );
}